* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: "Roboto", -apple-system, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.todo {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 750px;
  height: 530px;
  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 20px #99a7b4;
  border-radius: 10px;
  border: 1px solid #99a7b4;
  font-size: 14px;

  &__sidebar {
    background-color: #99a7b4;
    width: 200px;
    height: calc(100% - 120px);
    border-right: 1px solid #295683;
    padding: 60px 20px;
  }
}
