.list {
    margin-bottom: 30px;
    &__add-bottom {
      svg {
        path {
          stroke: #797272;
        }
      }
      span {
        color: #797272;
      }

      &:hover {
        color: #ffffff;
      }
    }

  li {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px 12px;
    cursor: pointer;

    &.active {
      background: #ffffff;
      box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.05);
      border-radius: 4px;
    }
  }

  i {
    display: inline-flex;
    margin-right: 8px;

    .badge {
      width: 10px;
      height: 10px;
      border-radius: 30px;
      position: relative;
      left: 5px;

      &--green {
        background-color: #37bb49;
      }

      &--blue {
        background-color: #2f18b3;
      }

      &--pink {
        background-color: #c43ead;
      }
    }
  }

}
